import React from "react";
import { Play, Eye, Calendar } from "lucide-react";
import Image from "next/image";

const HighlightCard = ({ video = {}, className = null, hidePlayButton = false, largeText = false }) => {
    const formatViews = (num:number) => {
        if (!num) return '0';
        if (num >= 1000000) return (num / 1000000).toFixed(1) + 'M';
        if (num >= 1000) return (num / 1000).toFixed(1) + 'k';
        return num.toString();
    };
    const formatDate = (dateString:string) => {
        if (!dateString) return '';
        const date = new Date(dateString);
        return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' });
    };

    return (
        <div className={`group relative overflow-hidden rounded-2xl cursor-pointer bg-slate-900 ${className}`}>
            {video.thumbnail_url ? (
                <Image src={video.thumbnail_url} alt={video.title} className="absolute inset-0 w-full h-full object-cover transition-transform duration-700 group-hover:scale-105" />
            ) : (
                <div className={`absolute inset-0 bg-gradient-to-br transition-transform duration-700 group-hover:scale-105`}></div>
            )}

            {/* Texture Overlay */}
            <div className="absolute inset-0 opacity-20 mix-blend-overlay bg-[url('https://www.transparenttextures.com/patterns/cubes.png')]"></div>

            {/* Dark Gradient Overlay for Text Readability */}
            <div className="absolute inset-0 bg-gradient-to-t from-black/95 via-black/40 to-transparent"></div>

            {/* Hover Reveal Effect */}
            <div className="absolute inset-0 bg-black/0 group-hover:bg-black/10 transition-colors duration-300"></div>

            {/* Content Container */}
            <div className="absolute inset-0 p-5 md:p-6 flex flex-col justify-between">

                {/* Top Header Row */}
                <div className="flex justify-between items-start z-10">
                    {video.category?.name && (
                        <span className="bg-white/20 backdrop-blur-md text-white text-[10px] uppercase font-bold px-2 py-1 rounded tracking-wider border border-white/10 shadow-sm">
                          {video.category.name}
                        </span>
                    )}
                </div>

                {/* Center Play Button (Optional) */}
                {!hidePlayButton && (
                    <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 opacity-0 group-hover:opacity-100 transition-all duration-300 transform scale-90 group-hover:scale-100 z-10">
                        <div className="w-14 h-14 rounded-full bg-white/20 backdrop-blur-md flex items-center justify-center border border-white/50 text-white shadow-lg">
                            <Play className="w-6 h-6 fill-current ml-1" />
                        </div>
                    </div>
                )}

                {/* Bottom Text Content */}
                <div className="z-10">
                    <h3 className={`font-bold text-white leading-tight mb-2 ${largeText ? 'text-2xl md:text-3xl' : 'text-lg md:text-xl'} group-hover:text-slate-200 transition-colors`}>
                        {video.title}
                    </h3>

                    {/* Use 'summary' from your data */}
                    {video.summary && largeText && (
                        <p className="text-slate-300 text-sm mb-4 line-clamp-2 hidden sm:block">
                            {video.summary}
                        </p>
                    )}

                    <div className="flex items-center gap-3 text-slate-300 text-xs font-medium">
            <span className="flex items-center gap-1.5" title={`${video.views_count} views`}>
              <Eye className="w-3 h-3" /> {formatViews(video.views_count)}
            </span>

                        <span className="w-1 h-1 rounded-full bg-slate-500"></span>

                        <span className="flex items-center gap-1.5">
                          <Calendar className="w-3 h-3" /> {formatDate(video.published_at)}
                        </span>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default HighlightCard;
